<script>
  // Imports
  import Card from "./Card.svelte";
  import { gsap } from "gsap";

  // Variables
  let Navigation_wrapper;
  let WaveOnClick;
  let WaveOnClick_Shadow;
  const mousePos = { x: 0, y: 0 };
  const Mouse_Animation_Hover = gsap.timeline();
  const Mouse_Animation_Click = gsap.timeline();
  Mouse_Animation_Hover.timeScale(10);
  Mouse_Animation_Click.timeScale(10);
  // Functions
  function Navigation_shine_animation() {
    const navigation_tl = gsap.timeline();
    setTimeout(() => {
      navigation_tl.to(".Shine", {
        left: `+=${Navigation_wrapper.offsetWidth + 200}`, // Get the width of the navbar width and add by 100
        duration: 0.9, // Duration of the timeline by 0.9s
        repeat: -1, // -1, Means repeat infinitely. If n > 0, then it means repeat n times.
        repeatDelay: 2 // For each repeat, how much seconds need to wait to start another repeat. In this case 2 seconds.
      });
    }, -1);
  }

  const Animate_Window_On_Hover = event => {
    mousePos.x = event.clientX;
    mousePos.y = event.clientY;
    Mouse_Animation_Hover.to(WaveOnClick_Shadow, {
      left: mousePos.x - 6,
      top: mousePos.y - 5,
      duration: 0.2
    });
  };

  const Animate_Window_On_Click = event => {
    mousePos.x = event.clientX;
    mousePos.y = event.clientY;
    Mouse_Animation_Click.play();
    Mouse_Animation_Click.to(WaveOnClick_Shadow, {
      zIndex: 999,
      scale: 20,
      opacity: 0,
      duration: 10,
      ease: "power1.out",
      onComplete: () => {
        Mouse_Animation_Click.restart().pause();
      }
    });
  };

  // Function Runner
  Navigation_shine_animation();
</script>

